.App {
  text-align: center;
}

* {
  font-family: "Inter", sans-serif;
  font-optical-sizing: auto;
  font-weight:bold;
  font-style: normal;
}

body {
  background-color: rgb(169, 169, 169);
}
header {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  height: 70px;
}

.logo {
  height: 60px;
  width: 60px;
}

.title {
  margin-right: 5%;
}


.signIn, .signOut {
  outline: none;
  cursor: pointer;
  line-height: 1;
  border-radius: 500px;
  transition-property: background-color,border-color,color,box-shadow,filter;
  transition-duration: .3s;
  border: 2px solid black;
  min-width: 100px;
  padding: 16px 14px 18px;
  color: #000000;
  box-shadow: inset 0 0 0 2px black;
  background-color: transparent;
  height: 48px;
}
.signIn:hover, .signOut:hover {
  color: #fff;
  background-color: rgb(139, 39, 143);
}

.add {
  height: 65px;
  width: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border: none;
  border-radius: 20%;
}

.add:hover {
  border: 3px solid black;
  cursor: pointer;
}

.addPuzzlesIcon {
  height: 90px;
  width: 90px;
  pointer-events: none;
  margin-right: 5px;
}


.userPhoto {
  height: 50px;
  width: 50px;
  border-radius: 50%;
  justify-self: end;
  border: 3px solid black;
  margin-right: 5px;
}

.userArea {
  display: flex;
  align-items: center;
  margin-left: 5%;
}

.puzzleBoard {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;
  padding: 50px 0px 50px 0px;
  border-top: 3px solid gray;
  background-color: rgb(169, 169, 169);
}

.puzzleCard {
  border-radius: 10%;
  border: 5px solid gray;
  width: 200px;
  height: 200px;
  display: grid;
  grid-template-columns: 60% 20% 20%;
  grid-template-rows: 60% 30% 10%;
  justify-items: start;
}

.X {
  background-color: #ffffff;
}
.G {
  background-color: rgb(0, 231, 0);
}
.R {
  background-color: rgb(228, 0, 0);
}

.puzzleCard:hover {
  border: 5px solid black;
}

.puzzleCard > .delete {
  height: 30px;
  width: 30px;
  justify-self: end;
  grid-column: 3 / 4;
  grid-row: 1 / 2;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 3px 3px 1px 1px;
  background-color: transparent;
  border: none;
  border-radius: 50%;
}

.delete:hover{
  background-color:rgb(169, 169, 169);
}

.delete:active{
  background-color: red;
}

.puzzleCard:hover .trashIcon {
  visibility: visible;
}
.puzzleCard > * > .trashIcon {
  pointer-events: none;
  height: 25px;
  width: 25px;
  visibility: hidden;
}

.puzzleCard > * > .favicon {
  width: 100px;
  height: 100px;
  border-radius: 15%;
  grid-column: 1 / 2;
  grid-row: 1 / 2;
  background-color: #ffffff;
}

.puzzleCard > p{
  grid-column: 1 / 3;
  grid-row: 2 / 3;
  margin: 0 0 10px 10px;
  align-self: end;
}

.puzzleCard > h5 {
  grid-column: 1 / 3;
  grid-row: 3 / 4;
  color: rgb(128, 128, 128);
  margin: 0 0 0 10px;
}

.puzzleCard > .addFromRecs {
  height: 30px;
  width: 30px;
  justify-self: end;
  grid-column: 3 / 4;
  grid-row: 1 / 2;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 3px 3px 1px 1px;
  background-color: transparent;
  border: none;
  border-radius: 50%;
}

.puzzleCard > * > .addIcon {
  pointer-events: none;
}

.addFromRecs:hover {
  background-color: rgb(169, 169, 169);
}

.addFromRecs:active {
  background-color: green;
}

.completedPuzzle, .failedPuzzle {
  height: 33px;
  width: 33px;
  grid-column: 2 / 3;
  grid-row: 1 / 2;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border: none;
  border-radius: 20%;
}

.completedPuzzle {
  align-self: start;
  justify-self:center;
  margin: 10px 0 0 0;
}

.failedPuzzle {
  align-self: end;
  justify-self:center;
  margin: 0 0 10px 0;
}

.completedPuzzle:hover {
  border: 3px solid rgb(0, 255, 0);
}
.failedPuzzle:hover {
  border: 3px solid rgb(255, 0, 0);
}

.successIcon, .failIcon {
  visibility: hidden;
  pointer-events: none;
}

.puzzleCard:hover .successIcon, .puzzleCard:hover .failIcon {
  visibility: visible;
}


@media only screen and (max-width: 500px) {
  .puzzleCard {
    height: 140px;
    width: 140px;
  }

  .puzzleCard > * > .favicon {
    width: 70px;
    height: 70px;
  }
  
  .title {
    display: none;
  }

  .userArea {
    margin-left: 0;
    margin-right: 2%;
  }

  .puzzleCard > h5 {
    display: none;
  }

  header {
    justify-content: space-between;
  }

  .add {
    margin-left: 25%;
  }
}
